/* Base styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
  line-height: 1.5;
}

.app-container {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  background-color: #fffbeb; /* amber-50 */
  color: #92400e; /* amber-800 */
}

.content {
  max-width: 28rem;
  width: 100%;
}

.header {
  text-align: center;
  margin-bottom: 2rem;
}

.header h1 {
  font-size: 1.875rem;
  font-weight: 700;
  color: #92400e; /* amber-800 */
}

.header p {
  color: #b45309; /* amber-700 */
  margin-top: 0.5rem;
}

/* Jar Container */
.jar-container {
  position: relative;
  display: flex;
  justify-content: center;
  padding: 2.5rem 0;
  margin-top: 2.5rem;
}

/* Cookie Animation */
.cookie-animation {
  position: absolute;
  z-index: 30;
}

.cookie {
  background-color: #fde68a; /* amber-200 */
  border-radius: 9999px;
  width: 3rem;
  height: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}

.cookie svg {
  color: #b45309; /* amber-700 */
}

/* Cookie Jar */
.cookie-jar {
  position: relative;
  cursor: pointer;
}

.jar-body {
  position: relative;
  width: 16rem;
  height: 18rem;
}

/* Jar Neck */
.jar-neck {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 10rem;
  height: 2.5rem;
  background-color: #fde68a; /* amber-200 */
  border-top-left-radius: 0.5rem;
  border-top-right-radius: 0.5rem;
  border: 2px solid #fcd34d; /* amber-300 */
  z-index: 10;
}

/* Jar Main Body */
.jar-main {
  position: absolute;
  top: 2rem;
  left: 50%;
  transform: translateX(-50%);
  width: 14rem;
  height: 16rem;
  background-color: rgba(254, 243, 199, 0.7); /* amber-100 with opacity */
  border-radius: 1rem;
  border: 2px solid #fcd34d; /* amber-300 */
  overflow: hidden;
}

/* Jar Contents */
.jar-contents {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 50%;
  background-color: #fcd34d; /* amber-300 */
  border-bottom-left-radius: 0.75rem;
  border-bottom-right-radius: 0.75rem;
}

/* Cookie Details */
.cookie-detail {
  position: absolute;
  border-radius: 9999px;
}

.cookie-1 {
  top: 1rem;
  left: 2rem;
  width: 2rem;
  height: 2rem;
  background-color: #d97706; /* amber-600 */
}

.cookie-2 {
  top: 2.5rem;
  left: 5rem;
  width: 2rem;
  height: 2rem;
  background-color: #d97706; /* amber-600 */
}

.cookie-3 {
  top: 1.5rem;
  left: 9rem;
  width: 2rem;
  height: 2rem;
  background-color: #d97706; /* amber-600 */
}

.cookie-4 {
  top: 3.5rem;
  left: 3rem;
  width: 1.5rem;
  height: 1.5rem;
  background-color: #b45309; /* amber-700 */
}

.cookie-5 {
  top: 4rem;
  left: 7rem;
  width: 1.5rem;
  height: 1.5rem;
  background-color: #b45309; /* amber-700 */
}

/* Jar Shine */
.jar-shine {
  position: absolute;
  top: 1rem;
  left: 1rem;
  width: 0.5rem;
  height: 10rem;
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 9999px;
}

/* Blessing Counter */
.blessing-counter {
  position: absolute;
  top: 3rem;
  right: 2rem;
  background-color: #fef3c7; /* amber-100 */
  border-radius: 9999px;
  padding: 0.25rem 0.5rem;
  font-size: 0.75rem;
  font-weight: 700;
  color: #92400e; /* amber-800 */
  z-index: 20;
}

/* Jar Lid */
.jar-lid {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 20;
}

.lid-base {
  position: relative;
}

.lid-top {
  width: 12rem;
  height: 1.5rem;
  background-color: #b45309; /* amber-700 */
  border-top-left-radius: 9999px;
  border-top-right-radius: 9999px;
}

.lid-bottom {
  width: 12rem;
  height: 1rem;
  background-color: #92400e; /* amber-800 */
  border-bottom-left-radius: 9999px;
  border-bottom-right-radius: 9999px;
}

.lid-knob {
  position: absolute;
  top: -1.5rem;
  left: 50%;
  transform: translateX(-50%);
  width: 2.5rem;
  height: 2rem;
  background-color: #92400e; /* amber-800 */
  border-top-left-radius: 9999px;
  border-top-right-radius: 9999px;
}

/* Form */
.blessing-form {
  margin-top: 2rem;
}

.form-group {
  margin-bottom: 1rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  color: #92400e; /* amber-800 */
}

.form-group input {
  width: 100%;
  padding: 0.5rem 0.75rem;
  border: 1px solid #fcd34d; /* amber-300 */
  border-radius: 0.375rem;
  outline: none;
  transition: box-shadow 0.2s, border-color 0.2s;
}

.form-group input:focus {
  border-color: #f59e0b; /* amber-500 */
  box-shadow: 0 0 0 3px rgba(245, 158, 11, 0.5);
}

.add-button {
  width: 100%;
  background-color: #d97706; /* amber-600 */
  color: white;
  border: none;
  border-radius: 0.375rem;
  padding: 0.5rem 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.2s;
}

.add-button:hover {
  background-color: #b45309; /* amber-700 */
}

.add-button:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

/* Blessings Display */
.blessings-display {
  margin-top: 2rem;
}

.blessings-card {
  padding: 1rem;
  background-color: #fef3c7; /* amber-100 */
  border: 1px solid #fcd34d; /* amber-300 */
  border-radius: 0.5rem;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
}

.blessings-card h2 {
  font-size: 1.25rem;
  font-weight: 700;
  color: #92400e; /* amber-800 */
  margin-bottom: 1rem;
}

.empty-state {
  color: #b45309; /* amber-700 */
  text-align: center;
  font-style: italic;
}

.blessing-items {
  list-style: none;
  padding: 0;
}

.blessing-item {
  padding: 0.5rem;
  background-color: white;
  border-radius: 0.5rem;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 0.5rem;
}

.blessing-item svg {
  color: #d97706; /* amber-600 */
}

.blessing-item span {
  color: #92400e; /* amber-800 */
}
